.portfolio {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }
  
  .portfolio-item {
    margin: 10px;
    text-align: center;
  }
  
  .portfolio-item img {
    max-width: 300px;
    height: auto;
    border: 2px solid #fff;
    border-radius: 5px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  }  